import React from 'react'
import { NavLink } from "react-router-dom"
import { CustomIcon } from "../index"
import { Button, Modal } from "react-materialize"
import { useCookies } from 'react-cookie'
import RegisterForm from "./RegisterForm"
import styles from "../../css.module/login.module.css"
import cx from "classnames";
import { useTranslation } from "react-i18next"

const LoginButton = ({ cardButton, sideNav }) => {
    const { t } = useTranslation()
    const [cookies] = useCookies(['user'])

    console.log(cardButton, sideNav)

    if (cardButton && cookies.user === undefined)
        return <Modal
            actions={[]} bottomSheet={false}
            header={t("")}
            id="Modal-12" open={false}
            options={{
                dismissible: true,
                endingTop: '10%',
                inDuration: 250,
                onCloseEnd: null,
                onCloseStart: null,
                onOpenEnd: null,
                onOpenStart: null,
                opacity: 0.5,
                outDuration: 250,
                preventScrolling: true,
                startingTop: '4%'
            }}
            trigger={<Button
                className="yellow darken-1"
                floating
                icon={<CustomIcon className={"notranslate"}>arrow_back</CustomIcon>}
                node="button"
                tooltip={t("  ")}
                tooltipOptions={{
                    position: 'left'
                }}
            />}
        >
            <RegisterForm inputId="register-form-input-cardButton" />
        </Modal>
    if (cardButton && cookies.user !== undefined)
        return <>
            <Button
                className="yellow darken-1"
                floating
                icon={<NavLink to="/login">{
                    <CustomIcon className={"notranslate"}>person</CustomIcon>
                }</NavLink>}
                node="button"
                tooltip={t(" ")}
                tooltipOptions={{
                    position: 'left'
                }}
            />

        </>
    if (sideNav && cookies.user === undefined)
        return <Modal
            actions={[]} bottomSheet={false}
            header={t("")}
            id="Modal-12" open={false}
            options={{
                dismissible: true,
                endingTop: '10%',
                inDuration: 250,
                onCloseEnd: null,
                onCloseStart: null,
                onOpenEnd: null,
                onOpenStart: null,
                opacity: 0.5,
                outDuration: 250,
                preventScrolling: true,
                startingTop: '4%'
            }}
            trigger={
                <NavLink to={""}>
                    <div className={cx('white-text')} style={{ paddingTop: 10, fontWeight: "bold", width: "100%", }}>
                        <div style={{ position: "relative", bottom: 40, marginTop: 30 }}>{t("")}</div>
                    </div>
                </NavLink>
            }>
            <RegisterForm inputId="register-form-input-sideNav" />
        </Modal>
    if (sideNav && cookies.user !== undefined)
        return <NavLink
            className='white-text'
            to="/login">{t(" ")}
        </NavLink>
    return <>
        {cookies.user === undefined ?
            <Modal
                actions={[]} bottomSheet={false}
                header={t("")}
                id="Modal-12" open={false}
                options={{
                    dismissible: true,
                    endingTop: '10%',
                    inDuration: 250,
                    onCloseEnd: null,
                    onCloseStart: null,
                    onOpenEnd: null,
                    onOpenStart: null,
                    opacity: 0.5,
                    outDuration: 250,
                    preventScrolling: true,
                    startingTop: '4%'
                }}
                trigger={
                    <Button
                        className={cx(styles.login, "z-depth-0")}
                        style={{
                            height: "100%",
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                        }}><CustomIcon
                            className="notranslate"
                        >
                            arrow_back
                        </CustomIcon>
                    </Button>}
            >
                <RegisterForm inputId="register-form-input-navBar" />
            </Modal > :
            <NavLink to="/login">{
                <CustomIcon className={"notranslate"}>person</CustomIcon>
            }</NavLink>
        }
    </>
}

export default LoginButton
